import Vue from 'vue'
import Router from 'vue-router'
// import MyHome from '@/components/pages/MyHome'
// import MyBlog from '@/components/pages/MyBlog'
// import MyWork from '@/components/pages/MyWork'
// import MyAbout from '@/components/pages/MyAbout'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      alias: '/home',
      name: 'MyContent',
      component: resolve => require(['@/components/pages/MyHome'], resolve)
    },
    {
      path: '/blog',
      name: 'MyBlog',
      component: resolve => require(['@/components/pages/MyBlog'], resolve)
    },
    {
      path: '/work',
      name: 'MyWork',
      component: resolve => require(['@/components/pages/MyWork'], resolve)
    },
    {
      path: '/about',
      name: 'MyAbout',
      component: resolve => require(['@/components/pages/MyAbout'], resolve)
    }
  ],
  mode: 'history'
})



